import $axios from '../plugins/ajax'

let reportService = {
  // TODO ()
  getReport(data = {}) {
    return $axios.ajax({
      url: '/trans/getModelData',
      data: Object.assign({
        refresh: true,
        dsCode: 'getStatementsList',
      }, data)
    });
  },
  // TODO ()
  getTotal(data = {}) {
    return $axios.ajax({
      url: '/trans/getModelData',
      data: Object.assign({
        refresh: true,
        dsCode: 'getAmountAndQty',
      }, data)
    });
  },
  // TODO ,
  getReportByDate(data = {}) {
    return $axios.ajax({
      url: '/trans/getModelData',
      data: Object.assign({
        refresh: true,
        dsCode: 'getStatementByTime',
      }, data)
    });
  },
  getTotalByDate(data = {}) {
    return $axios.ajax({
      url: '/trans/getModelData',
      data: Object.assign({
        refresh: true,
        dsCode: 'getQtyByTime',
      }, data)
    });
  },
  // TODO 
  getReportSummary(data = {}) {
    return $axios.ajax({
      url: '/trans/getModelData',
      data: Object.assign({
        refresh: true,
        dsCode: 'getPunchCard',
      }, data)
    });
  },
  // TODO 
  getUnreported(data = {}) {
    return $axios.ajax({
      url: '/trans/getModelData',
      data: Object.assign({
        refresh: true,
        dsCode: 'getNotpunchCardName',
      }, data)
    });
  }
}

export default reportService


