import conf from './conf';
import tokenService from '../../service/tokenService'

import axios from 'axios';

let Rxports = {
  /**
   * @param {String} type      post
   * @param {String} url        
   * @param {String} time      
   * @param {Object} data      
   * @param {String} dataType    xml html json ...
   * @param {String} contentType      headers
   * @param {Function} success    ,[data, res]
   * @param {Function} error      
   * @param return
   */
  ajax(opts = {}) {
    return new Promise((resolve, reject) => {
      tokenService.getToken().then(token => {
        if (!opts.url) {
          alert('');
          return false;
        }
        let params = {
          method: opts.type || 'GET',
          url: opts.url,
          headers: {
            'Content-Type': opts.contentType || '*/*',
            Authorization: token
          },
          // `baseURL`  `url`  `url`  URL
          //  `baseURL`  axios  URL
          baseURL: conf.base_url,
          timeout: opts.time || 10 * 1000,
          responseType: opts.dataType || 'json'
        }
        if (opts.type && opts.type.toUpperCase() === 'POST') {
          params.data = opts.data || {}
        } else {
          params.params = opts.data || {}
        }

        axios(params).then(function (res) {
          let data = res.data
          if (Number(res.status) === 200) {
            resolve(data)
          }
        }).catch(function (error) {
          let res = error.response
          let data = (res && res.data) || {}
          let status = (res && res.status) || 0
          let message = data.message || ''
          if (status === 401) {
            tokenService.login()
          }
          reject({
            success: false,
            message: message
          })
        });
      })
    })
  }
};

export default Rxports;





































