
import XButton from 'vux/src/components/x-button/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import querystring from 'vux/src/tools/querystring/index.js'

import tokenService from '../service/tokenService'
import Loading from './loading'

const ROSE_OPTION_KEY = 'ROSE_OPTION';
const ROSE_LOGIN_CODE = 'ROSE_CODE';
export default {
components: {
XButton,
Confirm,
Loading
},
data() {
return {
showLookReport: false, // 
showLoading: false,
username:'' 
}
},
methods: {
/*
* 
*/
goSP() {
let timestamp = new Date().getTime();
this.$router.push({path: `/saleReport?_ts=${timestamp}`})
},

gohelp() {
let timestamp = new Date().getTime();
this.$router.push({path: `/help?_ts=${timestamp}`})
},
/*
* 
*/
goMR() {
this.$router.push({path: '/myRecord'})
},
/*
* 
*/
goRP() {
this.$router.push({path: '/reportsOp'})
}
},
beforeCreate() {
let query = querystring.parse(location.search.slice(1));
let code = sessionStorage.getItem(ROSE_LOGIN_CODE);
if (query.code) { // codesession
sessionStorage.setItem(ROSE_LOGIN_CODE, query.code);
}
if (process.env.NODE_ENV !== 'development' && !code && !query.code) { // codesessionStorage
window.location.replace('https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx5311bd8608c14d98&redirect_uri=https%3a%2f%2fwww.gofuit.com%2fRose&response_type=code&scope=SCOPE&agentid=1000004&state=1#wechat_redirect')
}
},
created() {
(async () => {
this.showLoading = true;
await tokenService.getToken().catch(err => {

this.$vux.alert.show({
content: err.message
})
});
await tokenService.isPresident().then(data => {
this.username = data.nickname; 
if (`${data.statu}` === '1') { // statu1
this.showLookReport = true
}
}).catch(err=>{
this.$vux.alert.show({
content: err.message
})
});

await tokenService.getUser().then(data => {
localStorage.setItem(ROSE_OPTION_KEY, JSON.stringify({
bank: data.homeBank || '',         //
region: data.homeProvince || '',   //
dept: data.area || '',             //
groupName: data.groupName || '',    //
captain: data.bmName || '',        //
userCode:data.userCode || '',      //
}))
}).catch(err => {
this.$vux.alert.show({
content: err.message
})
});
this.showLoading = false;
})()
}
}



